<script>

import {store} from "@/store/store";

export default class PayplugFractionalOptions {

	title = null;
	name=null;
	class = null;
	description_1 = null;
	text_from = null;
	description_2 = null;
	links = [];
	notes = null;
	enabled = {
		checked: false,
		name: null
	};
	options = {};

	//TODO:: declare all options properties
	constructor(data){
		const mode = store.manageMode();

		this.title = data.title;
		this.class = data.class;
		this.name = data.name;
		this.description_1 = data.descriptions[mode].description_1;
		this.description_2 = data.descriptions[mode].description_2;
		this.links = data.descriptions[mode].links;
		this.notes = data.descriptions[mode].notes;
		this.text_from = data.descriptions[mode].text_from;
		this.options = data.options;
		this.enabled = data.enabled;
	}
}

</script>
