<script>

import PayplugFractionalOptions from "@/classes/PaymentMethods/PaymentOptions/PayplugFractionalOptions";
import PayplugDeferredOptions from "@/classes/PaymentMethods/PaymentOptions/PayplugDeferredOptions";

export default class PayplugStandardAdvanced {

	title = null;
	options = [];

	constructor(data)
	{
		let that = this;
		this.title = data.title;

		data.options.map(function (v){

			if(v.name ==="fractional"){
				that.options.push(new PayplugFractionalOptions(v));
			}

			if(v.name === "deferred"){
				that.options.push(new PayplugDeferredOptions(v));
			}

		});

	}

}

</script>

