<template>
	<div class="_inputs">
		<div style="margin-bottom: 8.5px;" v-if="option">
			<Paragraph :paragraphText="option.description_1" class="d-inline"/>
			<Anchor v-for="anchor in option.links" v-bind:key="anchor"
					:anchorHref="anchor.url"
					:anchorText="anchor.text"
					:anchorTitle="anchor.text"
					:anchorTarget="anchor.target"  />
		</div>
		<div style="margin-bottom: 8.5px;" v-if="option">
			<Paragraph :paragraphText="option.description_2" class="d-inline"/>
		</div>
		<SelectBox v-if="option.options"
			:selectName="option.options.name"
			:selectOptions="option.options.options"
			:selectDisabled="option.options.disabled"
			@updateDeferredValues="updateValue" />
	</div>

	<TextAlert v-if="warnings" :textAlertText="warnings" :textAlertType="'-warning'" />


</template>

<script>
import Paragraph from "@/components/atoms/AtomParagraph";
import Anchor from "@/components/atoms/AtomAnchor";
import SelectBox from "@/components/atoms/AtomSelect";
import TextAlert from "@/components/atoms/AtomTextAlert";


export default {
	name: "MoleculeDeferredPayment",
	components:{Paragraph, Anchor, SelectBox, TextAlert},
	computed:{
		warnings:{
			get(){
				return this.warning_msg;
			},
			set(newValue){
				this.warning_msg = newValue;
			}
		}
	},
	props: {
		option:{
			type: Object,
			required: true
		}
	},
	data() {
		return {
			warning_msg: ""
		}
	},
	methods: {
		updateValue(option){
			this.warnings = option.warning_msg ? option.warning_msg : '';
		}
	}
}
</script>

