<template>
	<div class="_inputs">
		<Paragraph :paragraphText="option.description_1" class="d-inline"/>
		<SelectBox v-if="instalmentOptions"
			:class="'installmentMode'"
			:selectName="instalmentOptions.name"
			:selectOptions="instalmentOptions.options"
			:selectDisabled="instalmentOptions.disabled"/>
		<Paragraph :paragraphText="option.text_from" />
		<div v-if="instalmentValue" class="payplugUIInput installmentMinAmount -icon -iconEuro -disabled">
			<AtomInput
				:inputType="'number'"
				:inputName="instalmentValue.name"
				:id="instalmentValue.name"
				:inputValue="instalmentValue.value"
				:inputMin="instalmentValue.min"
				:inputMax="instalmentValue.max"
				:inputStep="instalmentValue.step"
				:input-disabled="instalmentValue.disabled"
				:class="input_amount_error ? 'inputError' : null"/>
		</div>
	</div>

	<div class="_statement" v-if="instalmentValue && input_amount_error">
		<div class="_icon installmentErrorIcon">
			<Icon iconName="error_alert" />
		</div>
		{{instalmentValue.out_of_bound_msg}}
	</div>


	<div style="margin-bottom: 8.5px;" v-if="option">
		<Paragraph :paragraphText="option.description_2" class="d-inline"/>
		<Anchor v-for="anchor in option.links" v-bind:key="anchor"
				:anchorHref="anchor.url"
				:anchorText="anchor.text"
				:anchorTitle="anchor.text"
				:anchorTarget="anchor.target"  />
	</div>

	<TextAlert v-if="option?.notes" :textAlertText="option?.notes?.description" :textAlertType="option?.notes?.type" />


</template>

<script>
import Paragraph from "@/components/atoms/AtomParagraph";
import Anchor from "@/components/atoms/AtomAnchor";
import SelectBox from "@/components/atoms/AtomSelect";
import AtomInput from "@/components/atoms/AtomInput";
import TextAlert from "@/components/atoms/AtomTextAlert";
import Icon from "@/components/atoms/AtomIcon";
import {store} from "@/store/store";

export default {
	name: "MoleculeFractionalPayment",
	components:{Paragraph, Anchor, SelectBox, AtomInput, TextAlert, Icon},
	computed:{
		instalmentOptions: function () {
			let options = [];
			if(this.option && this.option.options){
				this.option.options.forEach(function(v){
					if(v.name==="payplug_inst_mode"){
						options = v;
					}
				});
			}
			return options;
		},
		instalmentValue: function(){
			let options = [];
			if(this.option && this.option.options){
				this.option.options.forEach(function(v){
					if(v.name==="payplug_inst_min_amount"){
						options = v;
					}
				});
			}
			return options;
		},
		input_amount_error: {
			get: () => store.data.enable_payplug_inst && ( parseInt(store.data.payplug_inst_min_amount) < 4 || parseInt(store.data.payplug_inst_min_amount) > 20000 )
		},
	},
	props: {
		option:{
			type: Object,
			required: true
		}
	}
}
</script>

