<template>
  <input
	:inputName="(inputName) ? inputName : null"
	:type="(inputType) ? inputType : null"
	:value="inputData"
	:step="(inputType=='number' && inputStep) ? inputStep : null"
	:min="(inputType=='number' && inputMin) ? inputMin : null"
	:max="(inputType=='number' && inputMax) ? inputMax : null"
	:disabled="(inputDisabled) ? 'disabled' : null"
	:placeholder="(inputPlaceholder) ? inputPlaceholder : null"
	@focusout="emitChange"
  />
</template>

<script>
import { store } from "@/store/store";

export default {
  name: "AtomInput",
	/*computed: {
		inputData:{
			get(){
				return this.inputValue ? this.inputValue : "";
			},
			set(newValue){
				console.log(newValue);
				this.inputData = newValue;
			}
		}
	},*/
	props: {
		inputName: {
			type: String,
			required: true,
			default: "text"
		},
		inputType: {
			type: String,
			required: true,
			default: "text"
		},
		inputDisabled: {
			type: String,
			required: false
		},
		inputValue: {
			type: String,
			required: false
		},
		inputStep: {
			type: String,
			required: false
		},
		inputMin: {
			type: String,
			required: false
		},
		inputMax: {
			type: String,
			required: false
		},
		inputPlaceholder: {
			type: String,
			required: false
		}
	},
	data() {
		return {
			store: store,
			inputData: this.inputValue ? this.inputValue : null
		}
	},
	created() {
		this.store.storeValue(this.inputName, this.inputValue || false)
	},
	methods: {
		emitChange(e) {
			let value = e.target.value

			if(this.inputType === "number"){
				value = parseInt(value);
			}

			//force setter
			this.inputData = value;
			this.store.storeValue(this.inputName, value)
		}
	}
}
</script>

